<template>
    <el-container>
        <el-aside class="left-group-aside" v-show="$getUserGroupSee()">
            <leftgroup
                :grouplist="groupCounts"
                @callGroup="callbackGroup"
            ></leftgroup>
        </el-aside>
        <el-main>
            <!--工具条-->
            <toolbar
                :buttonList="buttonList"
                @callFunction="callFunction"
            ></toolbar>

            <!--列表-->
            <el-table
                :data="users"
                v-loading="listLoading"
                :row-class-name="$tableRowClassName"
                style="width: 100%"
                @sort-change="sortList"
            >
                <!-- <el-table-column type="selection" width="50"> </el-table-column> -->
                <el-table-column type="index" width="80">
                    <template slot-scope="scope">
                        <div
                            v-text="(page - 1) * pageSize + 1 + scope.$index"
                        ></div>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="Name"
                    label="角色名"
                    width
                    sortable="custom"
                ></el-table-column>
                <el-table-column
                    prop="GROUPID"
                    label="全部分组"
                    width
                    sortable="custom"
                >
                    <template slot-scope="scope">
                        <el-tag disable-transitions>{{
                            scope.row.GROUPID == 0
                                ? "全分组共用"
                                : scope.row.GroupShortName
                        }}</el-tag>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="CreateTime"
                    label="创建时间"
                    width
                    sortable="custom"
                ></el-table-column>
                <el-table-column
                    prop="Enabled"
                    label="状态"
                    width="200"
                    sortable="custom"
                >
                    <template slot-scope="scope">
                        <el-tag
                            :type="scope.row.Enabled ? 'success' : 'danger'"
                            disable-transitions
                            >{{ scope.row.Enabled ? "正常" : "禁用" }}</el-tag
                        >
                    </template>
                </el-table-column>
                <el-table-column
                    label="操作"
                    width="150"
                    v-if="canEdit || canDelete"
                >
                    <template slot-scope="scope">
                        <el-button
                            v-if="canEdit"
                            plain
                            type="warning"
                            size="small"
                            @click="handleEdit(scope.$index, scope.row)"
                            >编辑</el-button
                        >
                        <el-button
                            v-if="canDelete"
                            plain
                            type="danger"
                            size="small"
                            @click="handleDel(scope.$index, scope.row)"
                            >删除</el-button
                        >
                    </template>
                </el-table-column>
            </el-table>

            <!--工具条-->
            <el-col :span="24" class="toolbar">
                <el-pagination
                    layout="total, prev, pager, next, jumper"
                    @current-change="handleCurrentChange"
                    :page-size="pageSize"
                    :current-page="page"
                    :total="total"
                    style="float: right"
                ></el-pagination>
            </el-col>

            <!--编辑界面-->
            <el-dialog
                title="编辑"
                :visible.sync="editFormVisible"
                v-model="editFormVisible"
                :close-on-click-modal="false"
            >
                <el-form
                    :model="editForm"
                    label-width="100px"
                    :rules="editFormRules"
                    ref="editForm"
                >
                    <el-row>
                        <el-col>
                            <el-form-item label="角色名" prop="Name">
                                <el-input
                                    maxlength="40"
                                    v-model="editForm.Name"
                                    auto-complete="off"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="状态" prop="Enabled">
                                <el-select
                                    v-model="editForm.Enabled"
                                    placeholder="请选择角色状态"
                                >
                                    <el-option
                                        v-for="item in statusList"
                                        :key="item.value"
                                        :label="item.name"
                                        :value="item.value"
                                    ></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12" v-show="$getUserGroupSee()">
                            <el-form-item label="所属分组" prop="GROUPID">
                                <el-select
                                    style="width: 100%"
                                    v-model="editForm.GROUPID"
                                    placeholder="请选择"
                                >
                                    <el-option
                                        label="全部分组共通"
                                        :value="0"
                                    ></el-option>
                                    <el-option
                                        v-for="item in groups"
                                        :key="item.GROUPID"
                                        :label="item.groupshortname"
                                        :value="item.GROUPID"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click.native="cancelEditDialog">取消</el-button>
                    <el-button
                        type="primary"
                        @click.native="editSubmit"
                        :loading="editLoading"
                        >提交</el-button
                    >
                </div>
            </el-dialog>

            <!--新增界面-->
            <el-dialog
                title="新增"
                :visible.sync="addFormVisible"
                v-model="addFormVisible"
                :close-on-click-modal="false"
            >
                <el-form
                    :model="addForm"
                    label-width="100px"
                    :rules="addFormRules"
                    ref="addForm"
                >
                    <el-row>
                        <el-col>
                            <el-form-item label="角色名" prop="Name">
                                <el-input
                                    v-model="addForm.Name"
                                    maxlength="40"
                                    auto-complete="off"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="状态" prop="Enabled">
                                <el-select
                                    style="width: 100%"
                                    v-model="addForm.Enabled"
                                    placeholder="请选择角色状态"
                                >
                                    <el-option
                                        label="激活"
                                        value="true"
                                    ></el-option>
                                    <el-option
                                        label="禁用"
                                        value="false"
                                    ></el-option>
                                </el-select> </el-form-item
                        ></el-col>
                        <el-col :span="12" v-show="$getUserGroupSee()">
                            <el-form-item label="所属分组" prop="GROUPID">
                                <el-select
                                    style="width: 100%"
                                    v-model="addForm.GROUPID"
                                    placeholder="请选择"
                                >
                                    <el-option
                                        label="全部分组共通"
                                        :value="0"
                                    ></el-option>
                                    <el-option
                                        v-for="item in groups"
                                        :key="item.GROUPID"
                                        :label="item.groupshortname"
                                        :value="item.GROUPID"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click.native="cancelAddDialog">取消</el-button>
                    <el-button
                        type="primary"
                        @click.native="addSubmit"
                        :loading="addLoading"
                        >提交</el-button
                    >
                </div>
            </el-dialog>
        </el-main>
    </el-container>
</template>

<script>
import util from "../../../util/date";
import leftgroup from "../../components/LeftGroup";
import {
    getRoleListPage,
    removeRole,
    editRole,
    addRole,
    getRoleGroups,
} from "../../api/api";
import { getButtonList } from "../../promissionRouter";
import Toolbar from "../../components/Toolbar";
import dicmixin from "../../assets/dic/dicmixin";
export default {
    components: { Toolbar, leftgroup },
    mixins: [dicmixin],
    data() {
        return {
            filters: {
                name: "",
            },
            buttonList: [],
            users: [],
            groupCounts: [],
            selectGroupId: 0,
            statusList: [
                { name: "激活", value: true },
                { name: "禁用", value: false },
            ],
            total: 0,
            page: 1,
            pageSize: 50,
            ordertype: "",
            order: "",
            listLoading: false,
            addDialogFormVisible: false,
            editFormVisible: false, //编辑界面是否显示
            editLoading: false,
            editFormRules: {
                Name: [
                    {
                        required: true,
                        message: "请输入角色名",
                        trigger: "blur",
                    },
                ],
                Enabled: [
                    { required: true, message: "请选择状态", trigger: "blur" },
                ],
                GROUPID: [
                    { required: true, message: "请选择分组", trigger: "blur" },
                ],
            },
            //编辑界面数据
            editForm: {
                Id: 0,
                Name: "",
                Enabled: false,
            },

            addFormVisible: false, //新增界面是否显示
            addLoading: false,
            addFormRules: {
                Name: [
                    {
                        required: true,
                        message: "请输入角色名",
                        trigger: "blur",
                    },
                ],
                Enabled: [
                    { required: true, message: "请选择状态", trigger: "blur" },
                ],
                GROUPID: [
                    { required: true, message: "请选择分组", trigger: "blur" },
                ],
            },
            //新增界面数据
            addForm: {
                Name: "",
                Enabled: true,
            },
            canEdit: false,
            canDelete: false,
            groups: [],
        };
    },
    methods: {
        callFunction(item) {
            this.filters = {
                name: item.search,
            };
            this[item.Func].apply(this, item);
        },
        //性别显示转换
        formatEnabled: function (row, column) {
            return row.Enabled ? "正常" : "未知";
        },
        formatCreateTime: function (row, column) {
            return !row.CreateTime || row.CreateTime == ""
                ? ""
                : util.formatDate.format(
                      new Date(row.CreateTime.replace(/-/g, "/")),
                      "yyyy-MM-dd"
                  );
        },
        handleCurrentChange(val) {
            this.page = val;
            this.getRoles();
        },
        //获取用户列表
        getRoles() {
            let _this = this;
            let para = {
                groupid: this.selectGroupId,
                page: this.page,
                size: this.pageSize,
                keyword: this.filters.name,
                order: this.order,
                ordertype: this.ordertype,
            };
            this.listLoading = true;
            //NProgress.start();
            getRoleListPage(para).then((res) => {
                this.total = res.data.dataCount;
                this.users = res.data.data;
                this.listLoading = false;

                //NProgress.done();
            });
        },
        //删除
        handleDel(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要删除的一行数据！",
                    type: "error",
                });

                return;
            }
            this.$confirm("确认删除该记录吗?", "提示", {
                type: "warning",
            })
                .then(() => {
                    this.listLoading = true;
                    let para = { id: row.Id };
                    removeRole(para).then((res) => {
                        if (util.isEmt.format(res)) {
                            this.listLoading = false;
                            return;
                        }
                        this.listLoading = false;
                        if (res.success) {
                            this.$message({
                                message: "删除成功",
                                type: "success",
                            });
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }

                        this.getRoles();
                        this.getGroupCounts();
                    });
                })
                .catch(() => {});
        },
        //显示编辑界面
        handleEdit(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要编辑的一行数据！",
                    type: "error",
                });

                return;
            }

            this.editFormVisible = true;
            this.editForm = Object.assign({}, row);
        },
        //显示新增界面
        handleAdd() {
            this.addFormVisible = true;
            this.addForm = {
                Name: "",
                Enabled: "",
            };
        },
        //编辑
        editSubmit: function () {
            this.$refs.editForm.validate((valid) => {
                if (valid) {
                    //this.$confirm("确认提交吗？", "提示", {}).then(() => {
                    this.editLoading = true;
                    let para = Object.assign({}, this.editForm);
                    editRole(para).then((res) => {
                        this.editLoading = false;
                        if (res.success) {
                            this.$message({
                                message: "修改成功",
                                type: "success",
                            });
                            this.cancelEditDialog();
                            this.getRoles();
                            this.getGroupCounts();
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }
                    });
                    //});
                }
            });
        },
        //新增
        addSubmit: function () {
            let _this = this;
            this.$refs.addForm.validate((valid) => {
                if (valid) {
                    //this.$confirm("确认提交吗？", "提示", {}).then(() => {
                    this.addLoading = true;
                    let para = Object.assign({}, this.addForm);
                    addRole(para).then((res) => {
                        this.addLoading = false;
                        if (res.success) {
                            this.$message({
                                message: "新增成功",
                                type: "success",
                            });
                            this.cancelAddDialog();
                            this.getRoles();
                            this.getGroupCounts();
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }
                    });
                    //});
                }
            });
        },
        cancelAddDialog() {
            this.$refs["addForm"].resetFields();
            this.addFormVisible = false;
        },
        cancelEditDialog() {
            this.$refs["editForm"].resetFields();
            this.editFormVisible = false;
        },
        sortList(column) {
            //alert("后台排序ing");
            if (column.order == "ascending") {
                this.ordertype = "asc";
            } else if (column.order == "descending") {
                this.ordertype = "desc";
            }
            this.order = column.prop;
            this.getRoles();
        },
        getGroupCounts() {
            getRoleGroups().then((res) => {
                if (res.success) {
                    this.groupCounts = res.data;
                }
            });
        },
        callbackGroup(item) {
            this.selectGroupId = item.GROUPID;
            this.page = 1;
            this.getRoles();
        },
    },
    mounted() {
        this.getRoles();
        this.getGroups();
        this.getGroupCounts();
        let routers = window.sessionStorage.router
            ? JSON.parse(window.sessionStorage.router)
            : [];

        let buttons = getButtonList(this.$route.path, routers);
        buttons.forEach((element) => {
            //&& element.IsButton && !element.IsHide
            if (element.Func == "handleEdit") {
                this.canEdit = true;
            } else if (element.Func == "handleDel") {
                this.canDelete = true;
            } else {
                this.buttonList.push(element);
            }
        });
    },
};
</script>

<style scoped>
</style>



